
@keyframes slide-up {
  0% {
    transform: translateY(50px);
  }
  100% {
    transform: translateY(0px);
  }
}

.slide-up {
	animation: slide-up 0.5s cubic-bezier(0.50, 0.460, 0.450, 0.940) both;
}

.centered {
  position: fixed; /* or absolute */
  top: 50%;
  left: 50%;

  
}


