/*------------------------------------
- COLOR warning
------------------------------------*/
.alert-warning {
    color: #e77800;
    background-color: #ff11612f;
    border-color: #ff10c11a;
}

.alert-warning hr {
    border-top-color: #ff100101;
}

.alert-warning .alert-link {
    color: #b45d00;
}

.badge-warning {
    color: #212529;
    background-color: #FFB463;
}

.badge-warning[href]:hover, .badge-warning[href]:focus {
    color: #212529;
    background-color: #ff9b30;
}

.bg-warning {
    background-color: #FFAC9E !important;
    border-color: "#df622c";
    box-shadow:
  0 1.8px 2.2px rgba(0, 0, 0, 0.02),
  0 4.3px 5.3px rgba(0, 0, 0, 0.028),
  0 8.1px 10px rgba(0, 0, 0, 0.035),
  0 14.5px 17.9px rgba(0, 0, 0, 0.042),
  0 27.2px 33.4px rgba(0, 0, 0, 0.05),
  0 65px 80px rgba(0, 0, 0, 0.07);
;

}





a.bg-warning:hover, a.bg-warning:focus,
button.bg-warning:hover,
button.bg-warning:focus {
    background-color: #ff9b30 !important;
}

.border-warning {
    border-color: #FFB463 !important;
}

.btn-warning {
    color: #212529;
    background-color: #FFB463;
    border-color: #FFB463;
    box-shadow:
    0 1.8px 2.2px rgba(0, 0, 0, 0.02),
    0 4.3px 5.3px rgba(0, 0, 0, 0.028),
    0 8.1px 10px rgba(0, 0, 0, 0.035),
    0 14.5px 17.9px rgba(0, 0, 0, 0.042),
    0 27.2px 33.4px rgba(0, 0, 0, 0.05),
    0 65px 80px rgba(0, 0, 0, 0.07);
  ;

}

.btn-warning:hover {
    color: #212529;
    background-color: #ffa23f;
    border-color: #ff9b30;
}

.btn-warning:focus, .btn-warning.focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 180, 99, 0.5);
}

.btn-warning.disabled, .btn-warning:disabled {
    color: #212529;
    background-color: #FFB463;
    border-color: #FFB463;
}

.btn-warning:not(:disabled):not(.disabled):active, .btn-warning:not(:disabled):not(.disabled).active, .show > .btn-warning.dropdown-toggle {
    color: #212529;
    background-color: #ff9b30;
    border-color: #ff9420;
}

.btn-warning:not(:disabled):not(.disabled):active:focus, .btn-warning:not(:disabled):not(.disabled).active:focus, .show > .btn-warning.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 180, 99, 0.5);
}

.btn-outline-warning {
    color: #FFB463;
    background-color: transparent;
    border-color: #FFB463;
}

.btn-outline-warning:hover {
    color: #212529;
    background-color: #FFB463;
    border-color: #FFB463;
}

.btn-outline-warning:focus, .btn-outline-warning.focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 180, 99, 0.5);
}

.btn-outline-warning.disabled, .btn-outline-warning:disabled {
    color: #FFB463;
    background-color: transparent;
}

.btn-outline-warning:not(:disabled):not(.disabled):active, .btn-outline-warning:not(:disabled):not(.disabled).active, .show > .btn-outline-warning.dropdown-toggle {
    color: #212529;
    background-color: #FFB463;
    border-color: #FFB463;
}

.btn-outline-warning:not(:disabled):not(.disabled):active:focus, .btn-outline-warning:not(:disabled):not(.disabled).active:focus, .show > .btn-outline-warning.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 180, 99, 0.5);
}

.list-group-item-warning {
    color: #e77800;
    background-color: #ff10c11a;
}

.list-group-item-warning.list-group-item-action:hover, .list-group-item-warning.list-group-item-action:focus {
    color: #e77800;
    background-color: #ff100101;
}

.list-group-item-warning.list-group-item-action.active {
    color: #212529;
    background-color: #e77800;
    border-color: #e77800;
}

.table-warning,
.table-warning > th,
.table-warning > td {
    background-color: #ff10c11a;
}

.table-hover .table-warning:hover {
    background-color: #ff100101;
}

.table-hover .table-warning:hover > td,
.table-hover .table-warning:hover > th {
    background-color: #ff100101;
}

.text-warning {
    color: #FFB463 !important;
}

a.text-warning:hover, a.text-warning:focus {
    color: #ff9b30 !important;
}

